import React from 'react';
import {Grid} from '@mui/material';
import TourComponent from './TourComponents/TourComponent';
import './TourList.scss';
import destinations from "./data/DestinationData";
import DestinationDetail from "./DestinationalDetail/DestinationDetail";

const TourList = ({filteredTours, isSearched, resultsRef}) => {
    console.log("Filtered Tours:", filteredTours); // Thêm dòng này để kiểm tra

    return (
        <div ref={resultsRef} className="container">
            <div className="content">
                {isSearched && (
                    <div>
                        <h6 style={{color: 'black', fontSize: '35px', margin: '0'}}>
                            Search Results:
                        </h6>
                        {filteredTours.length > 0 ? (
                            <Grid container spacing={3} style={{marginTop: '20px', marginBottom: '50px'}}>
                                {filteredTours.map(tour => (
                                    <Grid item xs={12} sm={6} md={4} key={tour.id}>
                                        <TourComponent tour={tour}/>
                                    </Grid>
                                ))}
                            </Grid>
                        ) : (
                            <h6 style={{color: 'black', fontSize: '35px', margin: '25px 0 0 0'}}>
                                Not Found
                            </h6>
                        )}
                    </div>
                )}
            </div>
            <h6 style={{margin: '20px 0', color: '#1a1a1a', fontSize: '35px', textAlign: 'center'}}>
                SELECT YOUR FAVOURITE DESTINATION! <div style={{fontSize: '25px', color: '#9d9595'}}>The most popular
                choices for visitors from Nha Trang</div>
            </h6>
            {destinations.map((destination, index) => (
                <DestinationDetail key={destination.id} destination={destination} reverseOrder={index % 2 !== 0}/>
            ))}
        </div>
    );
};

export default TourList;
