import React from "react";

function Slide() {
    return (
        <div className="img"
             style={{width: '100%', marginTop: '80px'}}>
            <img
                src={'https://images.pexels.com/photos/247616/pexels-photo-247616.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1'}
                alt="Gallery"
                style={{width: '100%'}}
            />
        </div>
    );
}

export default Slide